.element__title {
    max-width: 230px;
    margin: 0;
    font-weight: 900;
    font-size: 24px;
    line-height: 29px;
    color: #000;
    text-rendering: optimizeLegibility;
    position: relative;
    z-index: 1;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}